import * as d3 from 'd3'

const addGradient = (defs, direction, colors) => {
	let gradient;
	if (direction === 'bottom-right') {
		gradient = defs
			.append("linearGradient")
			.attr("id", "gradient")
			.attr("x1", "0%")
			.attr("y1", "0%")
			.attr("x2", "100%")
			.attr("y2", "100%")
	} else if (direction === 'bottom-left') {
		gradient = defs
			.append("linearGradient")
			.attr("id", "gradient")
			.attr("x1", "100%")
			.attr("y1", "0%")
			.attr("x2", "0%")
			.attr("y2", "100%")
	} else if (direction === 'top-right') {
		gradient = defs
			.append("linearGradient")
			.attr("id", "gradient")
			.attr("x1", "0%")
			.attr("y1", "100%")
			.attr("x2", "100%")
			.attr("y2", "0%")
	} else if (direction === 'top-left') {
		gradient = defs
			.append("linearGradient")
			.attr("id", "gradient")
			.attr("x1", "100%")
			.attr("y1", "100%")
			.attr("x2", "0%")
			.attr("y2", "0%")
	} else if (direction === 'center') {
		gradient = defs
			.append("radialGradient")
			.attr("id", "gradient")
			.attr("r", "0.8")
	}

	colors.reverse().forEach((color, index) =>
		gradient.append("stop")
			.attr('offset', ((100 / colors.length) * (index + 1)) + '%')
			.attr("stop-color", color.color)
			.attr('stop-opacity', 1)
		)
}

export default (hor, ver, x00, y00, w, h, id) => {
	if (!d3.select('.rect').empty()) localStorage.setItem('prevZoom', JSON.stringify(window.prevZoom || {}))
	const prevZoom = JSON.parse(localStorage.getItem('prevZoom') || '{}')

	let x0 = x00 / ((window.prevZoom && window.prevZoom.transform.k) || (prevZoom.transform && prevZoom.transform.k) || 1)
	let y0 = y00 / ((window.prevZoom && window.prevZoom.transform.k) || (prevZoom.transform && prevZoom.transform.k) || 1)

	localStorage.setItem('rectParams', JSON.stringify([
		hor,
		ver,
		x00,
		y00,
		w,
		h,
	]))

	d3.select('#clipGradient')
		.html('')
		.append('rect')
		.attr('x', x0)
		.attr('y', y0)
		.attr('width', w)
		.attr('height', h)

	d3.select('#rectsG').html('').append('g')
		.attr('id', id)
		.attr('class', 'rect')

	const svg = d3.select(`#${id}`)
		.append("g")
		.attr('id', 'mainG')
	
	svg.append('rect')
		.attr('x', x0)
		.attr('y', y0)
		.attr('width', w)
		.attr('height', h)
		.attr("stroke-width", 1)
		.attr("stroke", "black")
		.attr('fill', 'none')

	
	const rectsW = w / hor
	let wArr = []
	for (let i = rectsW; Math.round(i) < Math.round(w); i += rectsW) {
		wArr.push(i)
		svg.append("line")
			.attr("x1", i + x0)
			.attr("y1", 0 + y0)
			.attr("x2", i + x0)
			.attr("y2", h + y0)
			.attr("stroke-width", 1)
			.attr("stroke", "black")
	}
	wArr.push(w)
	const rectsH = h / ver
	let hArr = []
	for (let i = rectsH; Math.round(i) < Math.round(h); i += rectsH) {
		hArr.push(i)
		svg.append("line")
			.attr("x1", 0 + x0)
			.attr("y1", i + y0)
			.attr("x2", w + x0)
			.attr("y2", i + y0)
			.attr("stroke-width", 1)
			.attr("stroke", "black")
	}
	hArr.push(h)

	let linesCoords = [[0, 0, 0, 0]]
	for (let i = 0; i < wArr.length + hArr.length - 1; i++) {
		let x1, x2, y1, y2
		if (i <= wArr.length - 1) {
			x1 = wArr[i]
			y1 = 0
		} else {
			x1 = w
			y1 = (i - wArr.length + 1) * rectsH
		}

		if (i <= hArr.length - 1) {
			y2 = hArr[i]
			x2 = 0
		} else {
			y2 = h
			x2 = (i - hArr.length + 1) * rectsW
		}
		
		linesCoords.push([x1, y1, x2, y2])
		svg.append("line")
			.attr("x1", x1 + x0)
			.attr("y1", y1 + y0)
			.attr("x2", x2 + x0)
			.attr("y2", y2 + y0)
			.attr("stroke-width", 1)
			.attr("stroke", "black")
	}

	for (let i = 0; i < wArr.length + hArr.length - 1; i++) {
		let x1, x2, y1, y2
		if (i < wArr.length - 1) {
			x1 = wArr[i]
			y1 = 0
		} else {
			x1 = w
			y1 = (i - wArr.length + 1) * rectsH
		}

		if (i <= hArr.length - 1) {
			y2 = hArr[i]
			x2 = 0
		} else {
			y2 = h
			x2 = (i - hArr.length + 1) * rectsW
		}
		
		svg.append("line")
			.attr("x1", x1 + x0)
			.attr("y1", y1 + y0)
			.attr("x2", x2 + x0)
			.attr("y2", y2 + y0)
			.attr("stroke-width", 1)
			.attr("stroke", "black")
	}
	for (let i = 0; i < wArr.length + hArr.length; i++) {
		let x1, x2, y1, y2
		if (i < wArr.length - 1) {
			x1 = wArr[wArr.length - 2 - i]
			y1 = 0
		} else {
			x1 = 0
			y1 = (i - wArr.length + 1) * rectsH
		}

		if (i <= hArr.length - 1) {
			y2 = hArr[i]
			x2 = w
		} else {
			y2 = h
			x2 = w - ((i - hArr.length + 1) * rectsW)
		}
		
		svg.append("line")
			.attr("x1", x1 + x0)
			.attr("y1", y1 + y0)
			.attr("x2", x2 + x0)
			.attr("y2", y2 + y0)
			.attr("stroke-width", 1)
			.attr("stroke", "black")
	}
}

const formatter = (number) => {
   let str = ''
   const strNumber = String(number)
   
   for (let i = strNumber.length - 1; i >= 0; i--) {
      str = ((strNumber.length - i) % 3 === 0 ? ' ' : '') + strNumber[i] + str
   }
   return str
}

export const drawCharts = (colors, direction) => {
   const canvasSizes = document.getElementById('canvas').getBoundingClientRect()

   var margin = {top: 0, right: 0, bottom: 30, left: 40};
   var width = canvasSizes.width - margin.left - margin.right;
   var height = canvasSizes.height + 10 - margin.top - margin.bottom;
  
   var svg = d3.select("#canvas").append("svg")
      .attr("width", width + margin.left + margin.right)
      .attr("height", height + margin.top + margin.bottom)
      .append("g")
      .attr("transform","translate("+margin.left+","+margin.top+")")
  
   const yScale = d3.scaleLinear().range([height, 0]).domain([0, 100 * height / width])
   const xScale = d3.scaleLinear().range([0, width]).domain([0, 100])

   const yAxis = d3.axisLeft(yScale).tickSize(-canvasSizes.width).ticks(5).tickFormat(formatter);
   const yAxisGroup = svg.append("g").call(yAxis)
   
   const xAxis = d3.axisBottom(xScale).tickSize(-canvasSizes.height).ticks(10).tickFormat(formatter);
   const xAxisGroup = svg.append("g").attr('transform', `translate(0, ${height})`).call(xAxis)
  
   svg.append("text").text("Y")
      .attr("x", 0 - margin.left)
      .attr("y", height / 2)
   svg.append("text").text("X")
      .attr("x", width / 2)
      .attr("y", height + margin.bottom)
		.style("text-anchor", "middle");
		
	const rects = d3
		.select('#canvas')
		.append('svg')
		.attr('id', 'rects')
		.attr("clip-path", "url(#clip)")

	const gradientG = rects
		.append('g')
		.attr('id', 'gradientG')
		// .attr('transform', 'translate(41, 0)')
		.attr("clip-path", "url(#clipGradient)")
	
	gradientG.append('rect')
		.attr('width', 2 * width)
		.attr('height', 2 * width)
		.attr('x', -width + 41)
		.attr('y', -width + height)
		.attr('fill', 'url(#gradient)')
		
	const gradientDefs = gradientG
		.append("defs")
	gradientDefs
		.append("clipPath")
		.attr("id", "clipGradient")
	
	addGradient(
		gradientDefs, 
		direction, 
		colors
	)

	rects.append("defs").append("clipPath")
      .attr('transform', 'translate(40, 0)')
      .attr("id", "clip")
      .append("rect")
      .attr("width", width)
		.attr("height", height)
	
	rects.append('g')
		.attr('id', 'rectsG')

   var zoom = d3.zoom()
      .scaleExtent([1, 1])
      .translateExtent([[-width, -width + height],[width + 40, width + height + 20]])
      .on("zoom", zoomed);

   function zoomed() {
		d3.select('#rectsG').attr('transform', d3.event.transform)
		d3.select('#gradientG').attr('transform', d3.event.transform)
      xAxisGroup.call(xAxis.scale(d3.event.transform.rescaleX(xScale)));
      yAxisGroup.call(yAxis.scale(d3.event.transform.rescaleY(yScale)));
		window.prevZoom = {...d3.event}
   }
   d3.select('#rects').call(zoom)

   return [xScale, yScale]
}

export const changeGradient = (colors, direction) => {
   const defs = d3.selectAll("#gradientG defs")

   defs.select('#gradient')
      .remove()
   
   addGradient(defs, direction, colors)
}

export const hideLines = () => {
	console.log('hide')
	d3.selectAll('#rectsG line').attr('stroke-width', 0)
	d3.selectAll('#rectsG rect').attr('stroke-width', 0)
}

export const showLines = () => {
	d3.selectAll('#rectsG line').attr('stroke-width', 1)
	d3.selectAll('#rectsG rect').attr('stroke-width', 1)
}